<template>
  <!-- 歌曲列表 -->
  <div id="songList">
    <div
      class="songslist"
      v-for="(item,index) in songs"
      :key="index"
      :class="{active:currentIndex===index}"
    >
      <div class="songswrapper" @click.stop="playAudio(item,index)">
        <!-- <div class="songsindex">
          <span :class="{active:currentIndex===index}">
            <img src="~assets/img/player/pause2.png" v-show="currentIndex===index">
            </span>
        </div>-->
        <div class="songsinfo">
          <div class="songtitle">{{item.name}} {{item.alia[0]}}</div>
          <div
            class="singer"
            :class="{active:currentIndex===index}"
          >{{item.ar[0].name}} - {{item.al.name}}</div>
        </div>
        <span class="mv"></span>
        <div class="more">
          <i class="iconfont icon-more"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getSongUrl } from "network/api";
export default {
  name: "SongList",
  props: ["songs"],
  data() {
    return {
      currentIndex: null,
    };
  },
  methods: {
    async playAudio(song, index) {
      const songurl = await getSongUrl(song.id);
      // console.log(song);
      const currentPlay = {};
      currentPlay.url = songurl.data.data[0].url;
      currentPlay.id = song.id;
      currentPlay.singer = song.ar[0].name;
      currentPlay.albumPic = song.al.picUrl;
      currentPlay.name = song.name;
      this.$store.commit("addToCurrentPlay", currentPlay);
      this.$store.dispatch("AddToPlayList", currentPlay);
      this.currentIndex = index;
      // console.log(currentPlay);
    },
  }
};
</script>

<style scoped>
/* 歌曲列表  ↓*/
#songList {
  overflow: hidden;
  position: relative;
}
.songswrapper {
  width: 100%;
  display: flex;
  height: 45px;
  line-height: 45px;
  padding: 3px 0;
  border-top: 1px solid rgb(247, 245, 245);
}
/* .songsindex{
  flex: 0.7;
  text-align: center;
  opacity: .7;
  color: #707070;
  border-top:1px solid wh ;
}
.songsindex img{
  width: 20px;
} */
.songsinfo {
  flex: 4;
  margin-left: 8px;
  display: flex;
  flex-direction: column;
  line-height: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  min-width: 0;
}
.songtitle {
  font-size: 15px;
  margin-bottom: 4px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.singer {
  font-size: 12px;
  opacity: 0.7;
  color: #707070;
}
.mv {
  flex: 0.5;
}
.more {
  flex: 0.5;
  text-align: center;
}

.active {
  color: rgb(209, 5, 49);
}
</style>