<template>
  <div id="playlistdetail" class="wrapper">
    <DetailNavbar :description="playlist.description" :changeContext="changeContext" :name="playlist.name"></DetailNavbar>
    <Scroll :probeType="3" class="content" ref="scroll" :pull-up-load="true" @scroll="contentScroll">
      <AuthorMsg
        :coverimg="playlist.coverImgUrl"
        :name="playlist.name"
        :creator="playlist.creator"
        :shareCount="playlist.shareCount"
        :playcount="playlist.playCount"
        :description="playlist.description"
        :commentCount="playlist.commentCount"
      ></AuthorMsg>
      <div class="playall" ref="playlist">
        <span class="songlength">播放全部 (共{{songs.length}}首)</span>
        <span class="collect">+ 收藏({{playlist.subscribedCount}})</span>
      </div>
      <!-- 歌曲列表 -->
     
      <SongList :songs="songs"  id="songlist" ></SongList>
    </Scroll>
  </div>
</template>

<script>
import DetailNavbar from "./DetailNavbar";
import AuthorMsg from "./AuthorMsg";
import SongList from './SongList'

import { getListDetail } from "network/api";
// import { getSonUrl } from "network/api";
import { getSongDetail } from "network/api";

import Scroll from "components/common/Scroll";

export default {
  name: "PlaylistDetail",
  async created() {
    this.getSongData();
  },
  mounted() {
    this.$nextTick(()=>{
      this.listOffsetTop = this.$refs.playlist.offsetTop;
      // console.log(this.listOffsetTop);
    });
    this.$nextTick(() => {
      this.$refs.scroll.scroll.refresh();
    });
    // this.$storage.get('histroyList',currentPlay);
    // this.histroyList = JSON.parse(localStorage.getItem("histroyList"))
    // console.log(this.histroyList);
  },
  data() {
    return {
      //歌单列表
      playlist: {},
      //trackidid
      trackid: [],
      //歌曲id
      songid: [],
      //歌曲信息
      songs: [],
      listOffsetTop:0,
      changeContext:false,
    };
  },
  components: { DetailNavbar, AuthorMsg, Scroll , SongList},
  methods: {
    async getSongData() {
      //传进来的歌单id
      this.id = this.$route.params.id;
      //根据歌单id找到所在歌单的歌曲列表
      const { data: res } = await getListDetail({ id: this.id });
      this.playlist = res.playlist;
      // console.log(this.playlist);
      this.songid = res.playlist.trackIds;
      // console.log(this.songid);
      // 查找所有的id 获取所有的歌曲id
      const trackIds = this.playlist.trackIds.map(({ id }) => id);
      // 再保存到trackid中
      this.trackid = trackIds;
      // console.log(trackIds);
      //根据歌曲id 发送网络请求获取所有歌曲
      //获取歌曲详细信息
      const songDetails = await getSongDetail(trackIds);
      //将歌曲的详细信息保存到songs中
      this.songs = songDetails.data.songs;
      // console.log(this.songs);
    },

    // 页面滚动触发navbar事件
    contentScroll(position){
      // console.log(position);
      if(position.y < (-this.listOffsetTop)){
        this.changeContext = true;
        // console.log(this.changeContext);
      }else{
        this.changeContext = false;
        // console.log(this.changeContext);
      }
    }
  }
};
</script>

<style lang="less" scoped>
#playlistdetail {
  position: relative;
}
#songlist{
  position: relative;
}
.content {
  height: calc(100vh - 50px);
  overflow: hidden;
  position: absolute;
  top: 50px;
  bottom: 45px;
  left: 0;
  right: 0;
}

/* 播放全部 */
.playall {
  width: 100%;
  height: 9.722vw;
  line-height: 35px;
  display: flex;
  justify-content: space-between;
}
.collect {
  background-color: #ee4d41;
  background-image: linear-gradient(
    to right,
    rgba(226, 125, 125, 0),
    rgb(226, 77, 77)
  );
  color: rgb(240, 239, 239);
  font-size: 3.889vw;
  padding: 0 2.778vw;
}

</style>